*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Roboto",sans-serif;overflow-x:hidden;background-color:#111}.show_navlink{transform:translateX(0) !important}.btn{display:inline-block;color:#ccc;text-decoration:none;background-color:rgba(0,0,0,.5);transition:transform .2s;border-radius:10px}.btn:hover{transform:scale(1.1);box-shadow:1px 1px 10px #783232}.btn:active{transform:scale(0.9);box-shadow:inset 1px 1px 10px #783232}.container{max-width:850px;width:100%;margin:0 auto}@keyframes NavFadeEffect{from{opacity:0;transform:translateX(100px)}to{opacity:1;transform:translateX(0)}}.navbar{display:flex;justify-content:space-between;padding:.5rem 5%;align-items:center;position:relative;z-index:2}.nav_link{display:flex}.nav_link li{list-style-type:none}.nav_link li a{display:inline-block;text-decoration:none;padding:.5rem 1rem;color:#ccc;border-radius:10px;transition:transform .5s}.nav_link li a:hover{transform:scale(1.05);box-shadow:1px 1px 10px #783232}.nav_link li a:active{transform:scale(1);box-shadow:inset 1px 1px 10px #783232}.burger{cursor:pointer;display:none;z-index:4}.burger div{width:25px;height:3px;background-color:#ccc;margin:4px 0;transition:transform .1s}.burger:hover>div{transform:scale(1.1);box-shadow:1px 1px 10px #783232}.burger:active>div{transform:scale(0.8)}.showcase{position:relative;top:-80px;left:0;width:100vw;height:657px;border-bottom:8px solid #222;background-image:linear-gradient(to bottom, black, transparent)}.showcase:before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background:url(../assets/bg-home.e0c09f7605a27d2fa718e392607bc31e.jpg) no-repeat center center/cover;z-index:-1}.satellite{display:flex;height:100%;justify-content:center;align-items:center;text-transform:uppercase}.satellite img{width:100%;height:100%;max-width:400px;max-height:450px;animation-name:MoveSatellite;animation-duration:10s;animation-timing-function:linear;animation-iteration-count:infinite}.satellite .sat_text{color:#ccc;text-align:center}.satellite .sat_text h1{font-size:4rem;padding:0rem 2rem}.satellite .sat_text h3{font-size:2rem;padding:1rem 2rem}.satellite .sat_text a{padding:.5rem 1rem;font-size:1.3rem}.mission{text-align:center;color:#ccc;margin:-6rem auto 0rem auto;padding:1rem;border-bottom:8px solid #222}.mission h1{padding:1rem;font-size:3rem}.mission p{letter-spacing:.5px}@keyframes MoveSatellite{0%{transform:scale(1);opacity:.8}50%{transform:scale(0.2);opacity:.1}100%{transform:scale(1);opacity:.8}}.featured{border-bottom:8px solid #222;background-image:linear-gradient(transparent, #783232)}.featured h1{text-align:center;padding:1rem;font-size:2rem;color:#ccc}.project{display:grid;grid-template-columns:repeat(4, 1fr);justify-content:center;align-items:center;padding:1rem}.text{width:250px;height:175px;justify-self:center;position:relative;overflow:hidden;border-radius:10px;box-shadow:4px 4px 10px #966464,-4px -4px 10px #000}.text img{width:100%;height:100%;transition:transform .5s}.text a{position:absolute;top:0;left:0;width:100%;height:100%;text-align:center;padding-top:80px;font-size:2rem;text-decoration:none;color:#ccc;background-color:rgba(0,0,0,.8);text-transform:uppercase;transition:transform .5s;transform:translateX(100%)}.second a{transform:translateX(-100%)}.fourth a{transform:translateX(-100%)}.first:hover>a,.second:hover>a,.third:hover>a,.fourth:hover>a{transform:translate(0)}.first:hover>img,.second:hover>img,.third:hover>img,.fourth:hover>img{transform:scale(1.2)}.blogs{display:flex;justify-content:center;align-items:center;color:#ccc;text-transform:uppercase;text-align:center;border-bottom:8px solid #222;max-height:600px;height:100%;padding:1rem}.blog_video{width:100%;padding:1rem}.blog_video h1{font-size:1.4rem;padding:1rem}.blog_video img{max-width:350px;max-height:250px;width:100%;height:100%;border:10px solid #222;box-shadow:2px 2px 20px #222;border-radius:5px;margin:0 1rem}.latest_blog{width:100%}.latest_blog h1{padding:1rem}.latest_blog .blog{display:flex;justify-content:space-between;padding:2px;background-color:#222;max-width:450px;width:100%;margin:0 auto;padding:4px 6px;margin-bottom:5px;border-radius:10px}.latest_blog .blog img{width:110px;height:100px;border-radius:10px 0 0 10px}.latest_blog .blog .right{max-width:340px;width:100%;text-align:right}.latest_blog .blog .right h1{text-align:center;padding:5px 2px}.latest_blog .blog .right p{padding:4px}.latest_blog .blog .right a{padding:.5rem 1rem}.footer{padding:1rem;margin:0 4rem;text-align:center;color:#ccc}.footer a{color:#ccc}.footer i{padding:1rem;transition:transform .5s}.footer i:hover{transform:scale(1.2);color:#aaa}@media screen and (max-width: 1150px){.featured .project{grid-template-columns:repeat(2, 1fr);grid-gap:1rem 1rem}}@media screen and (max-width: 950px){.satellite .sat_text h1{padding:0 1rem;font-size:2.5rem}.satellite .sat_text h3{padding:1rem;font-size:1.3rem}.blogs{flex-direction:column}.blogs .blog_video{margin-bottom:2rem}.blogs .latest_blog{padding-bottom:4rem}}@media screen and (max-width: 750px){body{overflow-x:hidden}.nav_link{position:absolute;right:0;top:0;z-index:3;box-shadow:-10px -10px 30px #000,inset 4px 4px 10px #783232;background-color:rgba(50,0,0,.9);flex-direction:column;width:250px;height:649px;border-radius:10px 0 0 10px;justify-content:space-around;align-items:center;transition:transform .5s;transform:translateX(100%)}.nav_link li{opacity:0}.burger{display:block}.satellite{flex-direction:column}.satellite img{max-width:250px;max-height:250px}.satellite .sat_text h1{padding:0 1rem;font-size:3rem}.satellite .sat_text h3{padding:1rem}.featured{background-image:linear-gradient(transparent, transparent, #783232)}.featured h1{font-size:1.5rem}.featured .project{grid-template-columns:repeat(1, 1fr)}.mission h1{font-size:1.5rem}}@media screen and (max-width: 550px){.logo img{width:200px}.nav_link{height:626px}.satellite .sat_text h1{padding:2rem .5rem 0 .5rem;font-size:2.5rem}.satellite .sat_text h3{font-size:1rem;padding:1rem}.latest_blog .blog{max-width:320px}.latest_blog .blog img{display:none}.latest_blog .blog .right{text-align:center}.footer{padding:1rem 0}.footer i{padding:5px}}